
.profile-edit-form {
	background-color: white;
	border-radius: 10px;
	box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
	padding:20px;
	max-width: 600px;
	margin: 50px auto;
}

.form-control {
	border-radius: 5px;
	box-shadow: none;
}


.btn-update {
	background-color: #1abc9c;
	color: white;
	border: none;
	border-radius: 5px;
	transition: background-color 0.3s;
}

.btn-update:hover {
	background-color: #16a085;
}

.gender-label {
	margin-right: 15px;
}


.post-form {
	background-color: white;
	border-radius: 10px;
	box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
	padding: 30px;
	max-width: 500px;
	margin: 50px auto;
}

.caption{
	outline: none;
	height: 130px;
	width: 80%;
	margin-bottom: 20px;
	border: 1px solid gray;
	border-radius: 5px;
}
.caps{
	text-align: center;
}

.crp-form {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    padding: 30px;
    max-width: 700px;
    min-height: 300px;
    margin: 50px auto; 
    padding: 20px;
}