.signup-form {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    padding: 30px;
    max-width: 500px;
    margin: 50px auto; 
}

.form-control {
    border-radius: 5px;
    box-shadow: none; 
}
.form-control:focus {
    border-color: #1abc9c;
    box-shadow: 0 0 5px rgba(26, 188, 156, 0.5);
}
.btn-signup {
    background-color: #1abc9c;
    color: white;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
    width: 100%; 
}
.btn-signup:hover {
    background-color: #16a085; 
}
.text-center a {
    color: #1abc9c; 
    text-decoration: none; 
}
.text-center a:hover {
    text-decoration: underline; 
}
