.notActivatedNav{
  color: white;
}

.notActivatedNav:hover{
  color: yellow;
  transform: scale(1.1);
}

.activatedNav {
    color: #2979ff !important; 
    position: relative;
    transform: scale(1.1);
    text-decoration: none;
  }
  
  .activatedNav::after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background: linear-gradient(45deg, #2979ff, #bb86fc); 
    position: absolute;
    bottom: -5px;
    left: 0;
    border-radius: 5px;
    transition: all 0.3s ease;
  }
  

  

.mobnotActivatedNav{
  color: black;
  text-decoration: none;
}