.login-form {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    padding: 30px;
    max-width: 500px;
    min-height: 300px;
    margin: 50px auto; 
    padding: 20px;
}

h1 {
    text-align: center;
    margin-bottom: 20px;
    color: #1abc9c; 
}

.form-control {
    border-radius: 5px;
    box-shadow: none; 
}

.form-control:focus {
    border-color: #1abc9c; 
    box-shadow: 0 0 5px rgba(26, 188, 156, 0.5);
}

.btn-login {
    background-color: #1abc9c;
    color: white;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.btn-login:hover {
    background-color: #16a085; 
}

.text-center a {
    color: #1abc9c; 
    text-decoration: none; 
    text-align: center;
}

.text-center a:hover {
    text-decoration: underline; 
}

hr {
    border-top: 1px solid black; 
}

.text-center{
    display: flex;
    justify-content: center;
    text-align: center;
}