.mainPosts {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 20px;
}

.allPosts-details {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
}

.post-card {
	background-color: white;
	border-radius: 10px;
	box-shadow: 0 4px 10px rgba(193, 178, 178, 0.1);
	margin-bottom: 20px;
	padding: 15px;
	height: 430px;
	width: 350px;
	overflow: scroll;
	border: 1px solid gray;
	cursor: pointer;
	justify-content: center;
	text-align: center;
}

.post-card::-webkit-scrollbar,
.comment-input::-webkit-scrollbar {
	display: none;
}

.post-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.profile-photo {
	border-radius: 50%;
	width: 50px;
	height: 50px;
}

.username {
	font-weight: bold;
	margin-left: 10px;
}

.post-content {
	margin-top: 10px;
}

.post-image {
	width: 100%;
	height: 250px;
	border-radius: 8px;
	margin-top: 10px;
}

.post-footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 10px;
}

.like-form {
	display: flex;
	align-items: center;
}

.likes-count {
	font-size: large;
	font-weight: bold;
	margin-left: 5px;
}

.comment-form {
	display: flex;
	align-items: center;
	width: 83%;
}

.comment-input {
	border: 1px solid #ccc;
	border-radius: 5px;
	padding: 10px;
	margin-right: 2px;
	flex: 1;
	outline: none;
}

.btn-comment {
	background-color: #1abc9c;
	color: white;
	border: none;
	border-radius: 5px;
	padding: 5px 10px;
	cursor: pointer;
}

.btn-comment:hover {
	background-color: #16a085;
}

.comments-section {
	margin-top: 10px;
	display: flex;
}

.comment-item {
	margin-top: 5px;
	color: #555;
}

#visitPf {
	background-color: aqua;
	border: none;
	border-radius: 5px;
	color: purple;
	cursor: pointer;
	text-decoration: none;
}

#visitPf:hover {
	background-color: blue;
	color: springgreen;
}
