.profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f4f8;
  border-radius: 10px;
  padding: 30px;
  max-width: 700px;
  min-height: 500px;
  margin: 50px auto;
  padding: 20px;
}

.profile-card {
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  max-width: 600px;
  text-align: left;
}

.pfpd {
  text-align: center;
  justify-content: center;
}

.profile-card img {
  border-radius: 50%;
  border: 5px solid #1abc9c;
  margin-bottom: 15px;
}

.bio {
  margin-bottom: 20px;
  font-size: 14px;
  color: #555;
}

.row {
  display: flex;
  justify-content: space-between;
}

.col {
  flex: 1;
  margin-right: 15px;
}

.col:last-child {
  margin-right: 0;
}

b {
  color: #333;
}

p {
  margin: 5px 0;
  color: #666;
}

.row {
  margin-bottom: 10px;
}

.row {
  display: flex;
  justify-content: space-between;
}

.col {
  flex: 1;
  margin: 10px;
}

.col b {
  display: block;
  margin-bottom: 5px;
}

@media (max-width: 468px) {
  .row {
    flex-direction: column;
    align-items: flex-start;
  }

  .col {
    margin-right: 0;
    margin-bottom: 15px;
  }

  .profile-card {
    padding: 15px;
  }
}